import React from "react";
import { CInput, CSelect, CSwitch, CTextArea } from "@components";
import { Col, Empty, Form, Row, Collapse, Card } from "antd";

const CDepreciationProfile = ({
  isForms,
  isEmpty,
  handleChange,
  forms,
  errors,
  method,
  depreciationYear,
  periodFrequency,
  onFullDepreciation,
  onPeriodFrequency,
  onDepreciationYear,
  onMethod,
}) => {
  const { Panel } = Collapse;

  const formItemLayout = {
    labelCol: {
      xs: { span: 20 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 },
    },
  };

  return (
    <>
      {isEmpty && <Empty />}
      {isForms && (
        <>
          <Row>
            <Col span={24}>
              <Card title="Header" bordered={false}>
                <Form {...formItemLayout}>
                  <CInput
                    label="Code"
                    validate={errors.code}
                    name="code"
                    id="code"
                    value={forms.code}
                    isRequired
                    onChange={handleChange}
                  />
                  <CInput
                    value={forms.name}
                    label="Name"
                    validate={errors.name}
                    name="name"
                    id="name"
                    isRequired
                    onChange={handleChange}
                    style={{ marginTop: -20 }}
                  />
                  <CTextArea
                    value={forms.description}
                    label="Description"
                    validate={errors.description}
                    name="description"
                    id="description"
                    onChange={handleChange}
                    style={{ marginTop: -20 }}
                  />
                </Form>
              </Card>
            </Col>
            <Col span={24} style={{ paddingTop: 10 }}>
              <Collapse
                defaultActiveKey={["0"]}
                style={{ marginTop: 10, marginBottom: 10 }}
              >
                <Panel header="General Depreciation Profile" key="1">
                  <Row gutter={16}>
                    <Col className="gutter-row" span={6}>
                      <Form layout="vertical">
                        <CSelect
                          label="Method"
                          placeholder="Method"
                          defaultValue={forms.method}
                          value={forms.method}
                          validate={errors.method}
                          onChange={onMethod}
                          options={method}
                        />
                      </Form>
                    </Col>
                    <Col className="gutter-row" span={6}>
                      <Form layout="vertical">
                        <CInput
                          label="Percentage"
                          placeholder="Percentage"
                          id="percentage"
                          validate={errors.percentage}
                          value={forms.percentage}
                          onChange={handleChange}
                        />
                      </Form>
                    </Col>
                    <Col className="gutter-row" span={6}>
                      <Form layout="vertical">
                        <CSelect
                          label="Depreciation Year"
                          placeholder="Depreciation Year"
                          defaultValue={forms.depreciation_year}
                          value={forms.depreciation_year}
                          validate={errors.depreciation_year}
                          onChange={onDepreciationYear}
                          options={depreciationYear}
                        />
                      </Form>
                    </Col>
                    <Col className="gutter-row" span={6}>
                      <Form layout="vertical">
                        <CSelect
                          label="Period Frequency"
                          placeholder="Period Frequency"
                          defaultValue={forms.period_frequency}
                          value={forms.period_frequency}
                          validate={errors.period_frequency}
                          onChange={onPeriodFrequency}
                          options={periodFrequency}
                        />
                      </Form>
                    </Col>
                  </Row>
                  <Row gutter={16}>
                    <Col className="gutter-row" span={6}>
                      <Form layout="vertical">
                        <CSwitch
                          value={forms.full_depreciation}
                          onChange={onFullDepreciation}
                          label="Full Depreciation"
                          name="full_depreciation"
                        />
                      </Form>
                    </Col>
                  </Row>
                </Panel>
              </Collapse>
            </Col>
          </Row>
        </>
      )}
    </>
  );
};

export default CDepreciationProfile;
